<template>
	<view class="result">
		{{msg[flg]}}
		<button type="default" @tap="to_index()">回到主页</button>
		<!-- <button type="default" @tap="to_back()">返回</button> -->
	</view>
</template>

<script>
	export default{
		data(){
			return {
				flg:0,
				msg:[
					'请假成功',
					'撤销成功',
					'续假成功',
				]
			}
		},
		onLoad: function(option) {
			this.$data.flg = option.flg
		},
		methods:{
			to_back(){
				uni.navigateBack({
				    delta: 1
				});
			},
			to_index(){
				uni.redirectTo({
					url:'/pages/ask/index'
				})
			}
		}
	}
</script>

